import React, { useState, useEffect } from 'react';
import Select from 'react-select';
import { ref, get } from 'firebase/database';
import { database } from '../Firebase';

const specialCategories = ['interview', 'boilerplate', 'samplecode'];

export default function Programming() {
  const [languages, setLanguages] = useState([]);
  const [language, setLanguage] = useState(null);
  const [subLanguages, setSubLanguages] = useState([]);
  const [subLanguage, setSubLanguage] = useState(null);
  const [qnaData, setQnaData] = useState({});
  const [error, setError] = useState('');
  const [expandedQuestion, setExpandedQuestion] = useState(null);

  // Load all main languages (excluding 'languages' key)
  useEffect(() => {
    const langRef = ref(database, 'qna');
    get(langRef)
      .then((snapshot) => {
        const data = snapshot.val() || {};
        const filteredKeys = Object.keys(data).filter(
          (key) => key !== 'languages'
        );
        setLanguages(
          filteredKeys.map((lang) => ({
            value: lang,
            label: lang.toUpperCase(),
          }))
        );
      })
      .catch(() => setLanguages([]));
  }, []);

  // When language changes
  useEffect(() => {
    setSubLanguage(null);
    setSubLanguages([]);
    setQnaData({});
    setError('');
    setExpandedQuestion(null);

    if (!language) return;

    const langValue = language.value;

    if (specialCategories.includes(langValue)) {
      // Load sublanguages (excluding special categories)
      const subLangRef = ref(database, `qna/${langValue}`);
      get(subLangRef)
        .then((snapshot) => {
          const subs = snapshot.val() || {};
          const subLangKeys = Object.keys(subs).filter(
            (key) => !specialCategories.includes(key)
          );
          setSubLanguages(
            subLangKeys.map((sub) => ({ value: sub, label: sub.toUpperCase() }))
          );
        })
        .catch(() => {
          setSubLanguages([]);
          setError('Failed to load sublanguages.');
        });

      // Load main Q&A (root level)
      const mainQnaRef = ref(database, `qna/${langValue}`);
      get(mainQnaRef)
        .then((snapshot) => {
          const data = snapshot.val() || {};
          const flatData = {};
          for (const key in data) {
            if (typeof data[key] === 'string') {
              flatData[key] = data[key];
            }
          }
          setQnaData(flatData);
          setError('');
        })
        .catch(() => {
          setQnaData({});
          setError('Failed to load questions. Try again later.');
        });
    } else {
      // Regular language: load Q&A directly
      const qnaRef = ref(database, `qna/${langValue}`);
      get(qnaRef)
        .then((snapshot) => {
          const data = snapshot.val() || {};
          setQnaData(data);
          setError('');
        })
        .catch(() => {
          setQnaData({});
          setError('Failed to load questions. Try again later.');
        });
    }
  }, [language]);

  // When subLanguage changes
  useEffect(() => {
    setQnaData({});
    setError('');
    setExpandedQuestion(null);

    if (!language || !specialCategories.includes(language.value)) return;

    if (!subLanguage) {
      // Load root level Q&A
      const mainQnaRef = ref(database, `qna/${language.value}`);
      get(mainQnaRef)
        .then((snapshot) => {
          const data = snapshot.val() || {};
          const flatData = {};
          for (const key in data) {
            if (typeof data[key] === 'string') {
              flatData[key] = data[key];
            }
          }
          setQnaData(flatData);
          setError('');
        })
        .catch(() => {
          setQnaData({});
          setError('Failed to load questions.');
        });
    } else {
      // Load nested Q&A
      const nestedQnaRef = ref(
        database,
        `qna/${language.value}/${subLanguage.value}`
      );
      get(nestedQnaRef)
        .then((snapshot) => {
          const data = snapshot.val() || {};
          setQnaData(data);
          setError('');
        })
        .catch(() => {
          setQnaData({});
          setError('Failed to load nested questions.');
        });
    }
  }, [language, subLanguage]);

  const handleCopy = async (text) => {
    try {
      await navigator.clipboard.writeText(text);
      alert('Answer copied!');
    } catch {
      alert('Copy failed. Try again.');
    }
  };

  const toggleQuestion = (question) => {
    setExpandedQuestion(expandedQuestion === question ? null : question);
  };

  return (
    <div
      style={{
        display: 'flex',
        flexDirection: 'column',
        maxWidth: 1000,
        margin: '3rem auto',
        padding: '1rem',
        fontFamily: "'Segoe UI', Tahoma, Geneva, Verdana, sans-serif",
        boxShadow: '0 3px 12px rgba(0,0,0,0.1)',
        borderRadius: 10,
        backgroundColor: '#fff',
        gap: '2rem',
      }}
    >
      <div
        style={{
          display: 'flex',
          flexDirection: 'row',
          gap: '1rem',
          flexWrap: 'wrap',
          alignItems: 'center',
        }}
      >
        <div style={{ flex: 1, minWidth: 200 }}>
          <label
            style={{ fontWeight: 600, marginBottom: 8, display: 'block' }}
            htmlFor='language-select'
          >
            Select Language:
          </label>
          <Select
            inputId='language-select'
            options={languages}
            value={language}
            onChange={setLanguage}
            menuPortalTarget={
              typeof document !== 'undefined' ? document.body : null
            }
            styles={{
              menuPortal: (base) => ({ ...base, zIndex: 9999 }),
            }}
            placeholder='-- Select Language --'
            isClearable
          />
        </div>

        {specialCategories.includes(language?.value) &&
          subLanguages.length > 0 && (
            <div style={{ flex: 1, minWidth: 200 }}>
              <label
                style={{ fontWeight: 600, marginBottom: 8, display: 'block' }}
                htmlFor='sub-language-select'
              >
                Select Sub Language:
              </label>
              <Select
                inputId='sub-language-select'
                options={subLanguages}
                value={subLanguage}
                onChange={setSubLanguage}
                menuPortalTarget={
                  typeof document !== 'undefined' ? document.body : null
                }
                styles={{
                  menuPortal: (base) => ({ ...base, zIndex: 9999 }),
                }}
                placeholder={`-- All (main ${language.value.toUpperCase()}) --`}
                isClearable
              />
            </div>
          )}
      </div>

      <div style={{ flexGrow: 1 }}>
        {!language && (
          <p style={{ color: '#777', fontStyle: 'italic' }}>
            Please select a language to see questions.
          </p>
        )}
        {error && <p style={{ color: 'red' }}>{error}</p>}

        {language && !error && Object.keys(qnaData).length === 0 && (
          <p style={{ color: '#777' }}>
            No questions available for{' '}
            {specialCategories.includes(language.value)
              ? subLanguage
                ? subLanguage.label
                : language.label
              : language.label}
            .
          </p>
        )}

        {language && Object.keys(qnaData).length > 0 && (
          <ul style={{ listStyleType: 'none', padding: 0, margin: 0 }}>
            {Object.entries(qnaData).map(([question, answer]) => (
              <li
                key={question}
                style={{
                  borderBottom: '1px solid #ddd',
                  padding: '0.75rem 0',
                  cursor: 'pointer',
                }}
                onClick={() => toggleQuestion(question)}
              >
                <div
                  style={{
                    fontWeight: '600',
                    color: '#2c3e50',
                    display: 'flex',
                    justifyContent: 'space-between',
                    alignItems: 'center',
                    userSelect: 'none',
                  }}
                >
                  <span>{question}</span>
                  <span style={{ fontSize: 18 }}>
                    {expandedQuestion === question ? '−' : '+'}
                  </span>
                </div>

                {expandedQuestion === question && (
                  <div
                    style={{
                      marginTop: '0.75rem',
                      padding: '1rem',
                      paddingTop: '2.5rem',
                      position: 'relative',
                      border: '1px solid #ccc',
                      borderRadius: 6,
                      backgroundColor: '#f9f9f9',
                      color: '#333',
                    }}
                  >
                    <button
                      onClick={(e) => {
                        e.stopPropagation();
                        handleCopy(answer);
                      }}
                      style={{
                        position: 'absolute',
                        top: '0.5rem',
                        right: '0.5rem',
                        padding: '0.3rem 0.8rem',
                        fontSize: '0.85rem',
                        backgroundColor: '#3498db',
                        color: '#fff',
                        border: 'none',
                        borderRadius: 4,
                        cursor: 'pointer',
                      }}
                    >
                      📋 Copy
                    </button>
                    <p style={{ marginTop: 0, whiteSpace: 'pre-wrap' }}>
                      {answer}
                    </p>
                  </div>
                )}
              </li>
            ))}
          </ul>
        )}
      </div>
    </div>
  );
}
