import React, { useState, useEffect } from 'react';
import axios from 'axios';
import 'bootstrap/dist/css/bootstrap.min.css';

const YOUTUBE_API_KEY = 'AIzaSyBYMhZOWStMBvn7UIRJ3ULNfSkqJu4PJ3s';

export default function YouTube() {
  const [query, setQuery] = useState('');
  const [videos, setVideos] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);
  const [isMobile, setIsMobile] = useState(window.innerWidth < 768);

  useEffect(() => {
    const handleResize = () => {
      setIsMobile(window.innerWidth < 768);
    };
    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  const searchYouTube = async () => {
    if (!query.trim()) return;
    setLoading(true);
    setError(null);
    setVideos([]);

    try {
      const response = await axios.get(
        'https://www.googleapis.com/youtube/v3/search',
        {
          params: {
            part: 'snippet',
            q: query,
            key: YOUTUBE_API_KEY,
            maxResults: 10,
            type: 'video',
          },
        }
      );
      setVideos(response.data.items);
    } catch {
      setError('Failed to fetch videos. Please try again.');
    } finally {
      setLoading(false);
    }
  };

  const handleClear = () => {
    setQuery('');
    setVideos([]);
    setError(null);
  };

  return (
    <div
      style={{
        width: '100vw',
        minHeight: '100vh',
        padding: 20,
        fontFamily: "'Segoe UI', Tahoma, Geneva, Verdana, sans-serif",
        backgroundColor: '#f9f9f9',
        boxSizing: 'border-box',
      }}
    >
      <h2 style={{ textAlign: 'center', color: '#cc0000', marginBottom: 20 }}>
        Search YouTube Videos
      </h2>

      {/* Search Input + Buttons */}
      <div className='container mb-4'>
        <div className='row g-2 justify-content-center'>
          <div className='col-md-6 col-12'>
            <input
              type='text'
              value={query}
              onChange={(e) => setQuery(e.target.value)}
              placeholder='Type your search query here...'
              className='form-control'
              disabled={loading}
              onKeyDown={(e) => e.key === 'Enter' && searchYouTube()}
            />
          </div>

          <div className='col-auto'>
            <button
              onClick={searchYouTube}
              disabled={loading || !query.trim()}
              className='btn btn-danger fw-bold'
            >
              {loading ? 'Searching...' : 'Search'}
            </button>
          </div>

          <div className='col-auto'>
            <button
              onClick={handleClear}
              disabled={loading && !query && videos.length === 0}
              className='btn btn-secondary fw-semibold'
            >
              Clear
            </button>
          </div>
        </div>
      </div>

      {/* Error */}
      {error && (
        <p
          className='text-center text-danger fw-bold mb-4'
          style={{ maxWidth: 900, margin: '0 auto' }}
        >
          {error}
        </p>
      )}

      {/* Videos */}
      {videos.length > 0 && (
        <div
          className='container'
          style={{
            maxWidth: 900,
            display: 'flex',
            flexDirection: isMobile ? 'column' : 'row',
            gap: 20,
            flexWrap: 'wrap',
            justifyContent: 'center',
          }}
        >
          {videos.map((video) => (
            <div
              key={video.id.videoId}
              style={{
                flex: isMobile ? 'none' : '0 0 45%',
                backgroundColor: '#fff',
                borderRadius: 8,
                boxShadow: '0 2px 8px rgba(0,0,0,0.1)',
                padding: 12,
                boxSizing: 'border-box',
                marginBottom: isMobile ? 20 : 0,
              }}
            >
              <h5 style={{ marginBottom: 8 }}>{video.snippet.title}</h5>
              <iframe
                width='100%'
                height='250'
                src={`https://www.youtube.com/embed/${video.id.videoId}`}
                title={video.snippet.title}
                frameBorder='0'
                allow='accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture'
                allowFullScreen
                style={{ borderRadius: 8 }}
              />
              <p className='mt-2 text-muted' style={{ fontSize: 14 }}>
                Channel: {video.snippet.channelTitle}
              </p>
            </div>
          ))}
        </div>
      )}
    </div>
  );
}
