import React from 'react';
import { NavLink } from 'react-router-dom';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Collapse } from 'bootstrap';
import 'bootstrap/dist/js/bootstrap.bundle.min.js';

const navItems = [
  { label: 'Home', to: '/' },
  { label: 'Login', to: '/login' },
  { label: 'Language', to: '/prog' },
  { label: 'WebLive', to: '/weblive' },
  {
    label: 'AI',
    dropdown: [
      { label: 'Gemini', to: '/gemini' },
      { label: 'Wikipedia', to: '/wiki' },
      { label: 'Youtube', to: '/youtube' },
    ],
  },
];

function Navbar() {
  const handleNavLinkClick = () => {
    const navbarCollapse = document.getElementById('navbarNav');
    if (!navbarCollapse) return;

    if (navbarCollapse.classList.contains('show')) {
      setTimeout(() => {
        const collapseInstance = Collapse.getInstance(navbarCollapse);
        if (collapseInstance) {
          collapseInstance.hide();
        } else {
          navbarCollapse.classList.remove('show');
        }
      }, 200);
    }
  };

  return (
    <>
      <nav className='navbar navbar-expand-lg navbar-dark bg-primary shadow-sm fixed-top'>
        <div className='container'>
          <NavLink className='navbar-brand fw-bold text-uppercase' to='/'>
            AI Model
          </NavLink>
          <button
            className='navbar-toggler'
            type='button'
            data-bs-toggle='collapse'
            data-bs-target='#navbarNav'
            aria-controls='navbarNav'
            aria-expanded='false'
            aria-label='Toggle navigation'
          >
            <span className='navbar-toggler-icon'></span>
          </button>

          <div
            className='collapse navbar-collapse justify-content-end'
            id='navbarNav'
          >
            <ul className='navbar-nav'>
              {navItems.map((item) =>
                !item.dropdown ? (
                  <li className='nav-item' key={item.label}>
                    <NavLink
                      to={item.to}
                      className={({ isActive }) =>
                        'nav-link px-3' +
                        (isActive ? ' text-danger fw-semibold' : '')
                      }
                      onClick={handleNavLinkClick}
                    >
                      {item.label}
                    </NavLink>
                  </li>
                ) : (
                  <li className='nav-item dropdown' key={item.label}>
                    <a
                      href='#!'
                      className='nav-link dropdown-toggle px-3'
                      role='button'
                      data-bs-toggle='dropdown'
                      aria-expanded='false'
                    >
                      {item.label}
                    </a>
                    <ul className='dropdown-menu dropdown-menu-end'>
                      {item.dropdown.map((sub) => (
                        <li key={sub.label}>
                          <NavLink
                            to={sub.to}
                            className={({ isActive }) =>
                              'dropdown-item' +
                              (isActive ? ' text-danger fw-semibold' : '')
                            }
                            onClick={handleNavLinkClick}
                          >
                            {sub.label}
                          </NavLink>
                        </li>
                      ))}
                    </ul>
                  </li>
                )
              )}
            </ul>
          </div>
        </div>
      </nav>

      {/* Custom Styles */}
      <style>{`
        body {
          padding-top: 70px;
        }

        .nav-link {
          color: #fff !important;
          transition: color 0.3s ease-in-out;
        }

        .nav-link:hover {
          color:rgb(95, 6, 6) !important; /* green on hover */
        }

        .nav-link.text-danger {
          color: #dc3545 !important; /* red if active */
        }

        .dropdown-menu {
          background-color: #0d6efd !important;
        }

        .dropdown-item {
          color: #000 !important; /* black text */
          transition: color 0.3s ease-in-out;
        }

        .dropdown-item:hover {
          color:rgb(124, 8, 10) !important; /* green hover */
        }

        .dropdown-item.text-danger {
          color: #dc3545 !important; /* red active */
        }

        .navbar-brand {
          font-size: 1.3rem;
          letter-spacing: 1px;
        }
      `}</style>
    </>
  );
}

export default Navbar;
