import React, { useState } from 'react';
import axios from 'axios';

const apiOptions = [
  { label: 'Railway', url: 'https://websearchapipy-production.up.railway.app' },
  { label: 'Render', url: 'https://websearchapipy.onrender.com' },
];

export default function WebLive() {
  const [query, setQuery] = useState('');
  const [results, setResults] = useState([]);
  const [loading, setLoading] = useState(false);
  const [copiedIndex, setCopiedIndex] = useState(null);
  const [selectedAPI, setSelectedAPI] = useState(apiOptions[0].url);

  const fetchSearchResults = async () => {
    if (!query.trim()) return;
    setLoading(true);
    setResults([]);
    setCopiedIndex(null);

    try {
      const res = await axios.get(
        `${selectedAPI}/scrape?q=${encodeURIComponent(query)}`
      );
      const data = res.data.data;
      if (Array.isArray(data)) {
        setResults(data);
      } else {
        setResults(['❌ No results found. Try different keywords.']);
      }
    } catch (err) {
      console.error(err);
      setResults([
        '❌ Failed to fetch results. Please check your server or try again later.',
      ]);
    } finally {
      setLoading(false);
    }
  };

  const handleCopy = (text, index) => {
    navigator.clipboard.writeText(text);
    setCopiedIndex(index);
    setTimeout(() => setCopiedIndex(null), 2000);
  };

  const handleClear = () => {
    setQuery('');
    setResults([]);
    setCopiedIndex(null);
  };

  return (
    <div
      style={{
        maxWidth: '100%',
        width: '95%',
        margin: '3rem auto',
        padding: 20,
        fontFamily: "'Segoe UI', sans-serif",
        borderRadius: 12,
        backgroundColor: '#fff',
        boxShadow: '0 0 12px rgba(0,0,0,0.1)',
      }}
    >
      <h2 style={{ textAlign: 'center', color: '#2c3e50', marginBottom: 20 }}>
        Live Web Search
      </h2>

      {/* Top row: API selector + search input */}
      <div
        style={{
          display: 'flex',
          gap: 10,
          flexWrap: 'wrap',
          alignItems: 'center',
          marginBottom: 12,
        }}
      >
        <select
          value={selectedAPI}
          onChange={(e) => setSelectedAPI(e.target.value)}
          style={{
            width: '16vh',
            padding: 10,
            borderRadius: 8,
            border: '1.5px solid #ccc',
            fontSize: 14,
          }}
        >
          {apiOptions.map((opt, i) => (
            <option key={i} value={opt.url}>
              {opt.label}
            </option>
          ))}
        </select>

        <input
          type='text'
          value={query}
          onChange={(e) => setQuery(e.target.value)}
          placeholder='Type something to search...'
          onKeyDown={(e) => e.key === 'Enter' && fetchSearchResults()}
          style={{
            flex: 1,
            padding: 12,
            fontSize: 16,
            borderRadius: 8,
            border: '1.5px solid #ccc',
            outline: 'none',
            minWidth: '240px',
          }}
        />
      </div>

      {/* Buttons */}
      <div
        style={{
          display: 'flex',
          gap: 10,
          justifyContent: 'center',
          flexWrap: 'wrap',
        }}
      >
        <button
          onClick={fetchSearchResults}
          disabled={loading}
          style={{
            padding: '10px 20px',
            backgroundColor: '#2980b9',
            color: '#fff',
            border: 'none',
            borderRadius: 8,
            cursor: loading ? 'not-allowed' : 'pointer',
            fontWeight: 'bold',
            minWidth: 100,
          }}
        >
          {loading ? 'Searching...' : 'Search'}
        </button>

        <button
          onClick={handleClear}
          style={{
            padding: '10px 20px',
            backgroundColor: '#e74c3c',
            color: '#fff',
            border: 'none',
            borderRadius: 8,
            cursor: 'pointer',
            fontWeight: 'bold',
            minWidth: 100,
          }}
        >
          Clear
        </button>
      </div>

      {/* Results */}
      {results.length > 0 && (
        <div
          style={{
            marginTop: 30,
            padding: 16,
            backgroundColor: '#f9f9f9',
            borderRadius: 8,
            boxShadow: 'inset 0 0 6px #dcdcdc',
            maxHeight: '60vh',
            overflowY: 'auto',
          }}
        >
          <strong>Search Results:</strong>
          <ul style={{ marginTop: 10, paddingLeft: 20 }}>
            {results.map((text, index) => (
              <li key={index} style={{ marginBottom: 20 }}>
                <p style={{ whiteSpace: 'pre-wrap' }}>{text}</p>
                <button
                  onClick={() => handleCopy(text, index)}
                  style={{
                    marginTop: 6,
                    padding: '6px 12px',
                    backgroundColor:
                      copiedIndex === index ? '#27ae60' : '#3498db',
                    color: '#fff',
                    border: 'none',
                    borderRadius: 6,
                    cursor: 'pointer',
                    fontSize: 14,
                  }}
                >
                  {copiedIndex === index ? 'Copied!' : 'Copy Text'}
                </button>
                <hr style={{ marginTop: 16, borderTop: '6px solid #000' }} />
              </li>
            ))}
          </ul>
        </div>
      )}
    </div>
  );
}
