import React, { useState, useEffect, useRef } from 'react';
import axios from 'axios';
import 'bootstrap-icons/font/bootstrap-icons.css';
import 'bootstrap/dist/css/bootstrap.min.css';

export default function Gemini() {
  const [question, setQuestion] = useState('');
  const [chatHistory, setChatHistory] = useState([]);
  const [loading, setLoading] = useState(false);
  const [voices, setVoices] = useState([]);
  const [selectedVoice, setSelectedVoice] = useState(null);
  const [speechRate, setSpeechRate] = useState(1);
  const [speechVolume, setSpeechVolume] = useState(1);
  const chatEndRef = useRef(null);
  const inputRef = useRef(null);

  const API_KEY = 'AIzaSyAYbj5Mt8-REAdlnbWosTGJPXrVNd-9pEg';
  const url = `https://generativelanguage.googleapis.com/v1/models/gemini-1.5-flash:generateContent?key=${API_KEY}`;

  // 💡 Personal responses for specific questions
  const customResponses = [
    {
      keywords: [
        'who are you',
        'what are you',
        'your name',
        'introduce yourself',
        'who created you',
        'about you',
      ],
      response: `**I'm your friendly AI assistant created by Yogesh Jamnor.** 🤖  
I'm here to assist you with anything — tech help, emotional support, or just fun chat! 😊`,
    },
    {
      keywords: ['your creator', 'made you', 'developer', 'created by'],
      response: `**I was crafted with love and code by Yogesh Jamnor.** 💻❤️  
He's a passionate MERN stack developer who enjoys creating smart apps! 🚀`,
    },
    {
      keywords: ['are you gemini', 'google ai', 'is this gemini', 'gemini'],
      response: `Yes, I use **Gemini AI by Google** behind the scenes,  
but I was personalized and trained for you by **Yogesh Jamnor**! 🧠✨`,
    },
  ];

  useEffect(() => {
    const loadVoices = () => {
      const availableVoices = window.speechSynthesis.getVoices();
      setVoices(availableVoices);
      if (availableVoices.length > 0) {
        const defaultVoice = availableVoices.find((v) =>
          v.name.toLowerCase().includes('google')
        );
        setSelectedVoice(defaultVoice || availableVoices[0]);
      }
    };

    window.speechSynthesis.onvoiceschanged = loadVoices;
    loadVoices();

    setChatHistory([
      {
        role: 'model',
        text: formatCustomMarkdown(
          `😄 Hello, I am bot created by Yogesh! How can I help you today?`
        ),
      },
    ]);
  }, []);

  useEffect(() => {
    chatEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  }, [chatHistory]);

  const emojiResponse = (text) => {
    const emojiMap = {
      '😂': "Haha, that's funny! 😆",
      '😊': "That's great to hear! 😊",
      '😄': 'You seem happy! 😄',
      '😢': "Don't worry, I'm here for you. 🤗",
      '😔': "I'm sorry to hear that. 😔",
      '😠': "I understand you're upset. Let's stay calm and find a solution. 🙏",
      '😕': 'Let me clarify that for you. 😊',
      '❤️': 'Sending love back! ❤️',
      '👍': 'Got it! 👍',
      '🔥': "That's lit! 🔥",
      '🙏': "You're welcome! 🙏",
      '🎉': 'Congratulations! 🎉',
      '💪': 'Stay strong! 💪',
      '🤔': "I'm thinking... 🤔",
      '😎': 'Cool! 😎',
      '🥳': 'Party time! 🥳',
      '😭': 'Sending hugs! 🤗',
    };

    const matches = Object.keys(emojiMap).filter((emoji) =>
      text.includes(emoji)
    );

    if (matches.length > 0) {
      return [...new Set(matches)]
        .map((emoji) => emojiMap[emoji])
        .join('<br/>');
    } else if (/[\uD800-\uDBFF][\uDC00-\uDFFF]/.test(text)) {
      return "I see you're using emojis! 😄 Let me try to understand...";
    }
    return null;
  };

  const handleAsk = async () => {
    if (!question.trim()) return;
    setLoading(true);

    const updatedHistory = [...chatHistory, { role: 'user', text: question }];
    const emojiReact = emojiResponse(question);

    // 🔐 Normalize question for exact matching
    const normalize = (str) =>
      str
        .toLowerCase()
        .replace(/[^\w\s]/gi, '')
        .replace(/\s+/g, ' ')
        .trim();

    const normalizedQ = normalize(question);

    const matchedCustom = customResponses.find((entry) =>
      entry.keywords.some((kw) => normalize(kw) === normalizedQ)
    );

    let finalHistory = [...updatedHistory];
    if (emojiReact) {
      finalHistory.push({ role: 'model', text: emojiReact });
    }

    if (matchedCustom) {
      finalHistory.push({
        role: 'model',
        text: formatCustomMarkdown(matchedCustom.response),
      });
      setChatHistory(finalHistory);
      setQuestion('');
      setLoading(false);
      return;
    }

    try {
      const res = await axios.post(url, {
        contents: finalHistory.map((item) => ({
          role: item.role,
          parts: [{ text: item.text }],
        })),
      });

      const answer =
        res.data?.candidates?.[0]?.content?.parts?.[0]?.text || 'No answer.';

      setChatHistory([
        ...finalHistory,
        { role: 'model', text: formatCustomMarkdown(answer) },
      ]);
      setQuestion('');
    } catch (err) {
      setChatHistory([
        ...finalHistory,
        {
          role: 'model',
          text: `❌ Error: ${
            err.response?.data?.error?.message || err.message
          }`,
        },
      ]);
    } finally {
      setLoading(false);
    }
  };

  const handleSpeak = (text) => {
    const raw = stripHTML(text);
    if (!selectedVoice) return;
    const utterance = new SpeechSynthesisUtterance(raw);
    utterance.voice = selectedVoice;
    utterance.volume = speechVolume;
    utterance.rate = speechRate;
    utterance.pitch = 1;
    window.speechSynthesis.cancel();
    window.speechSynthesis.speak(utterance);
  };

  const handleStopSpeak = () => window.speechSynthesis.cancel();

  const handleCopy = (text) => {
    const raw = stripHTML(text);
    navigator.clipboard.writeText(raw);
  };

  const handleClear = () => {
    window.speechSynthesis.cancel();
    setChatHistory([
      {
        role: 'model',
        text: formatCustomMarkdown(
          `🙏 Thank you for the chat!  
          
😄 Hello, I am bot created by Yogesh! How can I help you today? ☺️`
        ),
      },
    ]);
    setQuestion('');
  };

  const formatCustomMarkdown = (text) => {
    let html = text
      .replace(/\*\*(.*?)\*\*/g, '<strong>$1</strong>')
      .replace(
        /^[-*] (.*)/gm,
        '<li style="list-style-type: disc;"><strong>$1</strong></li>'
      )
      .replace(
        /^\+ (.*)/gm,
        '<li style="list-style-type: square;"><strong>$1</strong></li>'
      )
      .replace(/\n{2,}/g, '</ul><br/><ul>');
    if (html.includes('<li')) html = `<ul>${html}</ul>`;
    return html;
  };

  const stripHTML = (html) => {
    const div = document.createElement('div');
    div.innerHTML = html;
    return div.textContent || div.innerText || '';
  };

  return (
    <div className='container py-4' style={{ maxWidth: '900px' }}>
      <div className='bg-white rounded shadow border p-3'>
        <h4 className='text-center text-primary mb-3'>
          Gemini AI Chatbot <i className='bi bi-stars'></i>
        </h4>

        <div
          className='p-3 mb-3'
          style={{
            height: '60vh',
            overflowY: 'auto',
            background: '#f8f9fa',
            borderRadius: '8px',
            border: '1px solid #dee2e6',
          }}
        >
          {chatHistory.map((item, index) => (
            <div
              key={index}
              className={`d-flex mb-3 ${
                item.role === 'user'
                  ? 'justify-content-end'
                  : 'justify-content-start'
              }`}
            >
              <div
                className={`p-3 rounded shadow-sm ${
                  item.role === 'user' ? 'bg-primary text-white' : 'bg-light'
                }`}
                style={{ maxWidth: '80%' }}
              >
                <div
                  dangerouslySetInnerHTML={{ __html: item.text }}
                  style={{ whiteSpace: 'pre-wrap' }}
                />
                {item.role === 'model' && (
                  <div className='mt-2 d-flex gap-2'>
                    <button
                      className='btn btn-sm btn-outline-secondary'
                      title='Copy'
                      onClick={() => handleCopy(item.text)}
                    >
                      <i className='bi bi-clipboard'></i>
                    </button>
                    <button
                      className='btn btn-sm btn-outline-primary'
                      title='Speak'
                      onClick={() => handleSpeak(item.text)}
                    >
                      <i className='bi bi-volume-up-fill'></i>
                    </button>
                    <button
                      className='btn btn-sm btn-outline-danger'
                      title='Stop'
                      onClick={handleStopSpeak}
                    >
                      <i className='bi bi-stop-fill'></i>
                    </button>
                  </div>
                )}
              </div>
            </div>
          ))}
          <div ref={chatEndRef}></div>
        </div>

        <textarea
          ref={inputRef}
          className='form-control mb-2'
          rows={3}
          placeholder='Ask something...'
          value={question}
          disabled={loading}
          onChange={(e) => setQuestion(e.target.value)}
          onKeyDown={(e) => {
            if (e.key === 'Enter' && !e.shiftKey) {
              e.preventDefault();
              handleAsk();
            }
          }}
        />

        <div className='d-flex gap-2 mb-3'>
          <button
            className='btn btn-success w-100'
            onClick={handleAsk}
            disabled={loading || !question.trim()}
          >
            {loading ? 'Thinking...' : 'Ask'}
          </button>
          <button className='btn btn-danger w-25' onClick={handleClear}>
            Clear
          </button>
        </div>

        <div className='bg-light rounded p-3'>
          <div className='mb-2'>
            <label className='form-label text-muted'>
              Speed: {speechRate.toFixed(2)}x
            </label>
            <input
              type='range'
              min='0.5'
              max='2'
              step='0.05'
              value={speechRate}
              onChange={(e) => setSpeechRate(parseFloat(e.target.value))}
              className='form-range'
            />
          </div>

          <div className='mb-2'>
            <label className='form-label text-muted'>
              Volume: {(speechVolume * 100).toFixed(0)}%
            </label>
            <input
              type='range'
              min='0'
              max='1'
              step='0.05'
              value={speechVolume}
              onChange={(e) => setSpeechVolume(parseFloat(e.target.value))}
              className='form-range'
            />
          </div>

          <div className='mb-2'>
            <label className='form-label text-muted'>Voice:</label>
            <select
              className='form-select'
              value={selectedVoice?.name || ''}
              onChange={(e) =>
                setSelectedVoice(voices.find((v) => v.name === e.target.value))
              }
            >
              {voices.map((v) => (
                <option key={v.name} value={v.name}>
                  {v.name} ({v.lang})
                </option>
              ))}
            </select>
          </div>
        </div>
      </div>
    </div>
  );
}
