import React, { useState } from 'react';
import axios from 'axios';
import Fuse from 'fuse.js';

export default function Wikipedia() {
  const [query, setQuery] = useState('');
  const [answer, setAnswer] = useState('');
  const [loading, setLoading] = useState(false);
  const [copied, setCopied] = useState(false);
  const [suggestions, setSuggestions] = useState([]);

  const fetchSummary = async (title) => {
    try {
      setLoading(true);
      setAnswer('');
      setCopied(false);
      setSuggestions([]);

      const res = await axios.get('https://en.wikipedia.org/w/api.php', {
        params: {
          action: 'query',
          prop: 'extracts',
          exintro: false,
          explaintext: true,
          titles: title,
          format: 'json',
          origin: '*',
        },
      });

      const pages = res.data.query.pages;
      const page = pages[Object.keys(pages)[0]];
      let content = page.extract || 'No content found.';
      content = content.replace(/\[\d+\]/g, ''); // remove ref numbers like [1]

      setAnswer(content);
    } catch (error) {
      console.error(error);
      setAnswer('Error fetching from Wikipedia or no result found.');
    } finally {
      setLoading(false);
    }
  };

  const searchWikipedia = async () => {
    if (!query.trim()) return;
    setLoading(true);
    setAnswer('');
    setCopied(false);
    setSuggestions([]);

    try {
      const searchRes = await axios.get('https://en.wikipedia.org/w/api.php', {
        params: {
          action: 'query',
          list: 'search',
          srsearch: query,
          format: 'json',
          origin: '*',
        },
      });

      const searchResults = searchRes.data.query?.search || [];

      if (searchResults.length === 0) {
        setAnswer('No matching topic found on Wikipedia.');
        setLoading(false);
        return;
      }

      const fuse = new Fuse(searchResults, {
        keys: ['title'],
        threshold: 0.4,
      });

      const fuseResult = fuse.search(query);

      const bestMatchTitle =
        fuseResult.length > 0
          ? fuseResult[0].item.title
          : searchResults[0].title;

      await fetchSummary(bestMatchTitle);

      setSuggestions(
        searchResults
          .filter((item) => item.title !== bestMatchTitle)
          .slice(0, 4)
      );
    } catch (error) {
      console.error(error);
      setAnswer('Error fetching from Wikipedia or no result found.');
      setLoading(false);
    }
  };

  const handleCopy = () => {
    if (!answer) return;
    navigator.clipboard.writeText(answer);
    setCopied(true);
    setTimeout(() => setCopied(false), 2000);
  };

  const handleClear = () => {
    setQuery('');
    setAnswer('');
    setCopied(false);
    setSuggestions([]);
  };

  return (
    <div
      style={{
        maxWidth: 700,
        margin: '3rem auto',
        padding: 20,
        fontFamily: "'Segoe UI', Tahoma, Geneva, Verdana, sans-serif",
        boxShadow: '0 4px 15px rgba(0,0,0,0.1)',
        borderRadius: 12,
        backgroundColor: '#fff',
        display: 'flex',
        flexDirection: 'column',
        gap: '1.5rem',
      }}
    >
      <h2 style={{ textAlign: 'center', color: '#2c3e50', marginBottom: 0 }}>
        Ask Wikipedia
      </h2>

      <input
        type='text'
        value={query}
        onChange={(e) => setQuery(e.target.value)}
        placeholder='Ask about a topic...'
        style={{
          width: '100%',
          padding: 12,
          fontSize: 16,
          borderRadius: 8,
          border: '1.5px solid #ccc',
          outline: 'none',
          transition: 'border-color 0.3s',
        }}
        onFocus={(e) => (e.target.style.borderColor = '#2980b9')}
        onBlur={(e) => (e.target.style.borderColor = '#ccc')}
        onKeyDown={(e) => {
          if (e.key === 'Enter') searchWikipedia();
        }}
      />

      <div
        style={{
          display: 'flex',
          gap: 12,
          flexWrap: 'wrap',
          justifyContent: 'center',
        }}
      >
        <button
          onClick={searchWikipedia}
          disabled={loading}
          style={{
            flexGrow: 1,
            minWidth: 120,
            padding: 12,
            fontWeight: '700',
            color: '#fff',
            backgroundColor: '#2980b9',
            border: 'none',
            borderRadius: 8,
            cursor: loading ? 'not-allowed' : 'pointer',
            transition: 'background-color 0.3s',
          }}
          onMouseEnter={(e) =>
            !loading && (e.currentTarget.style.backgroundColor = '#1f5f8b')
          }
          onMouseLeave={(e) =>
            !loading && (e.currentTarget.style.backgroundColor = '#2980b9')
          }
        >
          {loading ? 'Searching...' : 'Get Answer'}
        </button>

        <button
          onClick={handleClear}
          disabled={loading && !query && !answer}
          style={{
            flexGrow: 1,
            minWidth: 120,
            padding: 12,
            fontWeight: '700',
            color: '#fff',
            backgroundColor: '#e74c3c',
            border: 'none',
            borderRadius: 8,
            cursor: loading ? 'not-allowed' : 'pointer',
            transition: 'background-color 0.3s',
          }}
          onMouseEnter={(e) =>
            !loading && (e.currentTarget.style.backgroundColor = '#b03028')
          }
          onMouseLeave={(e) =>
            !loading && (e.currentTarget.style.backgroundColor = '#e74c3c')
          }
        >
          Clear
        </button>
      </div>

      {suggestions.length > 0 && (
        <div
          style={{
            padding: 12,
            backgroundColor: '#eef2f7',
            borderRadius: 8,
            boxShadow: 'inset 0 0 6px #c9d6e3',
          }}
        >
          <strong>Did you mean:</strong>
          <ul
            style={{
              marginTop: 10,
              paddingLeft: 20,
              listStyleType: 'disc',
            }}
          >
            {suggestions.map((s) => (
              <li key={s.pageid} style={{ marginBottom: 8 }}>
                <button
                  onClick={() => fetchSummary(s.title)}
                  style={{
                    background: 'none',
                    border: 'none',
                    color: '#2980b9',
                    cursor: 'pointer',
                    fontSize: 16,
                    textDecoration: 'underline',
                    padding: 0,
                  }}
                >
                  {s.title}
                </button>
              </li>
            ))}
          </ul>
        </div>
      )}

      {answer && (
        <div
          style={{
            backgroundColor: '#f9f9f9',
            padding: 20,
            borderRadius: 10,
            position: 'relative',
            whiteSpace: 'pre-wrap',
            boxShadow: '0 0 6px rgba(0,0,0,0.1)',
          }}
        >
          <strong style={{ display: 'block', marginBottom: 12, fontSize: 18 }}>
            Answer:
          </strong>

          <p style={{ marginBottom: 0, color: '#333', lineHeight: 1.5 }}>
            {answer}
          </p>

          <button
            onClick={handleCopy}
            style={{
              position: 'absolute',
              top: 16,
              right: 16,
              backgroundColor: copied ? '#27ae60' : '#3498db',
              color: '#fff',
              border: 'none',
              borderRadius: 6,
              padding: '8px 14px',
              cursor: 'pointer',
              fontWeight: '600',
              fontSize: 14,
              transition: 'background-color 0.3s',
            }}
            onMouseEnter={(e) =>
              !copied && (e.currentTarget.style.backgroundColor = '#2e86c1')
            }
            onMouseLeave={(e) =>
              !copied && (e.currentTarget.style.backgroundColor = '#3498db')
            }
          >
            {copied ? 'Copied!' : 'Copy'}
          </button>
        </div>
      )}
    </div>
  );
}
