import React, { useEffect, useState } from 'react';
import { ref, get, remove, set } from 'firebase/database';
import { database } from '../Firebase';
import { useNavigate } from 'react-router-dom';
import Cookies from 'js-cookie';

export default function Language() {
  const [languages, setLanguages] = useState([]);
  const [newLang, setNewLang] = useState('');
  const [username, setUsername] = useState('');
  const [email, setEmail] = useState('');
  const navigate = useNavigate();

  const [isMobile, setIsMobile] = useState(window.innerWidth <= 768);

  // ✅ Check login from Cookies
  useEffect(() => {
    const user = Cookies.get('user');
    if (!user) {
      navigate('/');
      return;
    }
    try {
      const parsed = JSON.parse(user);
      setUsername(parsed.username);
      setEmail(parsed.email);
    } catch {
      Cookies.remove('user');
      navigate('/');
    }
  }, [navigate]);

  useEffect(() => {
    const fetchLanguages = async () => {
      try {
        const langRef = ref(database, 'qna/languages');
        const snapshot = await get(langRef);
        const langs = snapshot.val() || [];
        setLanguages(langs);
      } catch {
        setLanguages([]);
      }
    };
    fetchLanguages();
  }, []);

  const addLanguage = async () => {
    if (!newLang.trim()) return;
    const lowerCaseLang = newLang.toLowerCase();
    if (languages.includes(lowerCaseLang)) {
      alert('Language already exists.');
      return;
    }
    const updated = [...languages, lowerCaseLang];
    await set(ref(database, 'qna/languages'), updated);
    setLanguages(updated);
    setNewLang('');
  };

  const deleteLanguage = async (lang) => {
    if (
      !window.confirm(
        `Are you sure you want to delete "${lang.toUpperCase()}"?`
      )
    )
      return;

    await remove(ref(database, `qna/${lang}`));
    const updated = languages.filter((l) => l !== lang);
    await set(ref(database, 'qna/languages'), updated);
    setLanguages(updated);
  };

  const handleLogout = () => {
    Cookies.remove('user');
    navigate('/');
  };

  useEffect(() => {
    const handleResize = () => setIsMobile(window.innerWidth <= 768);
    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  const styles = {
    container: {
      display: 'flex',
      flexDirection: isMobile ? 'column' : 'row',
      height: '100vh',
      fontFamily: "'Segoe UI', Tahoma, Geneva, Verdana, sans-serif",
      backgroundColor: '#f1f3f5',
      overflow: 'auto',
    },
    sidebar: {
      width: isMobile ? '100%' : 280,
      background: '#fff',
      borderRight: isMobile ? 'none' : '1px solid #dee2e6',
      borderBottom: isMobile ? '1px solid #dee2e6' : 'none',
      padding: '1.5rem 1rem',
      overflowY: 'auto',
      maxHeight: isMobile ? '40vh' : '100vh',
      boxSizing: 'border-box',
    },
    sidebarHeader: {
      display: 'flex',
      justifyContent: 'space-between',
      alignItems: 'center',
      marginBottom: '1rem',
    },
    sidebarTitle: {
      color: '#343a40',
      fontWeight: '600',
      fontSize: '1.3rem',
    },
    logoutBtnSidebar: {
      backgroundColor: '#e74c3c',
      color: '#fff',
      border: 'none',
      padding: '6px 12px',
      borderRadius: 6,
      cursor: 'pointer',
      fontSize: 14,
      fontWeight: 'bold',
      userSelect: 'none',
    },
    langItem: {
      display: 'flex',
      justifyContent: 'space-between',
      alignItems: 'center',
      padding: '0.5rem 0.75rem',
      backgroundColor: '#f8f9fa',
      borderRadius: 5,
      marginBottom: '0.5rem',
      border: '1px solid #dee2e6',
      fontWeight: '500',
      fontSize: '1rem',
    },
    deleteBtn: {
      backgroundColor: '#dc3545',
      color: '#fff',
      border: 'none',
      padding: '0.25rem 0.5rem',
      borderRadius: 4,
      cursor: 'pointer',
      fontWeight: 'bold',
      fontSize: '1.2rem',
      lineHeight: 1,
      userSelect: 'none',
    },
    mainArea: {
      flex: 1,
      padding: '2rem',
      position: 'relative',
      overflowY: 'auto',
      backgroundColor: '#fff',
      maxHeight: isMobile ? '60vh' : '100vh',
      boxSizing: 'border-box',
    },
    heading: {
      color: '#343a40',
      marginBottom: '1rem',
      fontWeight: '700',
      fontSize: '1.8rem',
    },
    input: {
      padding: '0.75rem',
      width: '100%',
      maxWidth: 400,
      borderRadius: 6,
      border: '1px solid #ced4da',
      fontSize: '16px',
      marginBottom: '1rem',
      background: '#fff',
      boxSizing: 'border-box',
    },
    addBtn: {
      backgroundColor: '#28a745',
      color: '#fff',
      padding: '0.6rem 1.2rem',
      fontSize: '16px',
      border: 'none',
      borderRadius: 6,
      cursor: 'pointer',
      fontWeight: 600,
      userSelect: 'none',
    },
    noLangText: {
      fontStyle: 'italic',
      color: '#6c757d',
    },
  };

  return (
    <div style={styles.container}>
      {/* Sidebar: Language List + Logout */}
      <div style={styles.sidebar}>
        <div style={styles.sidebarHeader}>
          <h3 style={styles.sidebarTitle}>Languages</h3>
          <button
            onClick={handleLogout}
            title='Logout'
            style={styles.logoutBtnSidebar}
            type='button'
          >
            🔒 Logout
          </button>
        </div>

        <p style={{ marginBottom: 10, fontSize: 14, color: '#555' }}>
          👤 {username} <br />
          📧 {email}
        </p>

        {languages.length === 0 ? (
          <p style={styles.noLangText}>No languages found.</p>
        ) : (
          languages.map((lang) => (
            <div key={lang} style={styles.langItem}>
              <span>{lang.toUpperCase()}</span>
              <button
                onClick={() => deleteLanguage(lang)}
                style={styles.deleteBtn}
                title={`Delete ${lang.toUpperCase()}`}
                aria-label={`Delete ${lang.toUpperCase()}`}
                type='button'
              >
                ×
              </button>
            </div>
          ))
        )}
      </div>

      {/* Main area: Add Language */}
      <main style={styles.mainArea}>
        <h2 style={styles.heading}>Add New Language</h2>

        <input
          type='text'
          placeholder='Enter new language name'
          value={newLang}
          onChange={(e) => setNewLang(e.target.value)}
          style={styles.input}
          aria-label='New language name'
        />

        <button onClick={addLanguage} style={styles.addBtn} type='button'>
          ➕ Add Language
        </button>
      </main>
    </div>
  );
}
