import React, { useState, useEffect } from 'react';
import { useNavigate, NavLink } from 'react-router-dom';
import { auth, provider } from '../Firebase';
import { signInWithPopup } from 'firebase/auth';
import Cookies from 'js-cookie';

export default function Login() {
  const [user, setUser] = useState(null);
  const [error, setError] = useState('');
  const [emailInput, setEmailInput] = useState('');
  const [passwordInput, setPasswordInput] = useState('');
  const navigate = useNavigate();

  const localUsers = [
    {
      email: 'jamnoryr@gmail.com',
      password: 'Yogesh@25235',
      username: 'Yogesh Jamnor',
    },
    {
      email: 'yogeshjamnor@gmail.com',
      password: 'Yogesh@25235',
      username: 'Yogesh Jamnor',
    },
  ];

  const isAllowedEmail = (email) =>
    ['yogeshjamnor@gmail.com', 'jamnoryr@gmail.com'].includes(email);

  useEffect(() => {
    const cookieData = Cookies.get('user');
    if (cookieData) {
      try {
        const parsed = JSON.parse(cookieData);
        setUser(parsed);
      } catch {
        Cookies.remove('user');
      }
    }
  }, []);

  const handleGoogleLogin = async () => {
    try {
      const result = await signInWithPopup(auth, provider);
      const googleUser = result.user;
      const email = googleUser.email;

      if (!isAllowedEmail(email)) {
        alert('❌ Access denied: Not authorized.');
        return;
      }

      const userInfo = {
        username: googleUser.displayName,
        email,
      };

      Cookies.set('user', JSON.stringify(userInfo), { expires: 7 });
      setUser(userInfo);
    } catch (err) {
      console.error('Google login failed:', err.message);
      setError('Google login failed. Try again.');
    }
  };

  const handleLocalLogin = (e) => {
    e.preventDefault();
    const matchedUser = localUsers.find(
      (u) => u.email === emailInput && u.password === passwordInput
    );

    if (!matchedUser) {
      setError('Invalid email or password.');
      return;
    }

    const userInfo = {
      username: matchedUser.username,
      email: matchedUser.email,
    };

    Cookies.set('user', JSON.stringify(userInfo), { expires: 7 });
    setUser(userInfo);
    setError('');
  };

  const handleLogout = () => {
    Cookies.remove('user');
    setUser(null);
  };

  return (
    <div
      style={{
        maxWidth: 400,
        margin: '5rem auto',
        padding: '2.5rem',
        boxShadow: '0 4px 16px rgba(0,0,0,0.1)',
        borderRadius: '12px',
        backgroundColor: '#ffffff',
        fontFamily: "'Segoe UI', Tahoma, Geneva, Verdana, sans-serif",
        textAlign: 'center',
      }}
    >
      {!user ? (
        <>
          <h2 className='mb-4' style={{ color: '#007bff' }}>
            Welcome Back 👋
          </h2>
          {error && (
            <p style={{ color: 'tomato', marginBottom: '1rem' }}>{error}</p>
          )}

          {/* Local Login Form */}
          <form onSubmit={handleLocalLogin} style={{ marginBottom: '1.5rem' }}>
            <input
              type='email'
              placeholder='Email'
              value={emailInput}
              onChange={(e) => setEmailInput(e.target.value)}
              className='form-control mb-3'
              style={{
                padding: '0.6rem',
                borderRadius: 8,
                border: '1px solid #ccc',
              }}
              required
            />
            <input
              type='password'
              placeholder='Password'
              value={passwordInput}
              onChange={(e) => setPasswordInput(e.target.value)}
              className='form-control mb-3'
              style={{
                padding: '0.6rem',
                borderRadius: 8,
                border: '1px solid #ccc',
              }}
              required
            />
            <button
              type='submit'
              style={{
                width: '100%',
                padding: '0.7rem',
                backgroundColor: '#007bff',
                color: '#fff',
                fontWeight: 'bold',
                border: 'none',
                borderRadius: 8,
              }}
            >
              Login with Email
            </button>
          </form>

          {/* Google Login */}
          <button
            onClick={handleGoogleLogin}
            style={{
              width: '100%',
              padding: '0.75rem',
              backgroundColor: '#4285F4',
              color: '#fff',
              fontWeight: 'bold',
              fontSize: '1rem',
              border: 'none',
              borderRadius: 8,
              cursor: 'pointer',
            }}
          >
            Sign in with Google
          </button>
        </>
      ) : (
        <>
          <h3 style={{ color: '#007bff' }}>Hello, {user.username} 👋</h3>
          <p style={{ fontSize: '0.95rem', color: '#666' }}>{user.email}</p>

          <div className='d-grid gap-2 mt-3'>
            <NavLink
              to='/language'
              className='btn'
              style={{
                backgroundColor: '#007bff',
                color: '#fff',
                borderRadius: 8,
                padding: '0.6rem',
                fontWeight: 'bold',
              }}
            >
              ➕ Add Language
            </NavLink>

            <NavLink
              to='/add'
              className='btn'
              style={{
                backgroundColor: '#17a2b8',
                color: '#fff',
                borderRadius: 8,
                padding: '0.6rem',
                fontWeight: 'bold',
              }}
            >
              ➕ Add Question
            </NavLink>

            <button
              onClick={handleLogout}
              className='btn mt-3'
              style={{
                backgroundColor: 'tomato',
                color: '#fff',
                borderRadius: 8,
                padding: '0.6rem',
                fontWeight: 'bold',
              }}
            >
              Logout
            </button>
          </div>
        </>
      )}
    </div>
  );
}
